import { useState, useEffect } from 'react';

/**
 * Hook pour d√©tecter si l'appareil est mobile
 * Utilise plusieurs m√©thodes de d√©tection pour une pr√©cision maximale
 */
export const useMobileDetection = () => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkIfMobile = () => {
      // M√©thode 1: User Agent
      const userAgent = navigator.userAgent.toLowerCase();
      const isMobileUA = /android|webos|iphone|ipad|ipod|blackberry|iemobile|opera mini/i.test(userAgent);

      // M√©thode 2: Largeur de l'√©cran
      const isMobileScreen = window.innerWidth <= 768;

      // M√©thode 3: Touch support
      const hasTouchSupport = 'ontouchstart' in window || navigator.maxTouchPoints > 0;

      // M√©thode 4: Orientation mobile
      const isMobileOrientation = window.orientation !== undefined;

      // M√©thode 5: DPI √©lev√© (appareils haute r√©solution)
      const isHighDPI = window.devicePixelRatio > 1.5;

      // Consid√®re comme mobile si au moins 3 crit√®res sont remplis
      const mobileCriteria = [isMobileUA, isMobileScreen, hasTouchSupport, isMobileOrientation, isHighDPI];
      const mobileScore = mobileCriteria.filter(Boolean).length;

      setIsMobile(mobileScore >= 3);
    };

    // V√©rification initiale
    checkIfMobile();

    // R√©√©coute lors du redimensionnement
    window.addEventListener('resize', checkIfMobile);
    
    // R√©√©coute lors du changement d'orientation
    window.addEventListener('orientationchange', checkIfMobile);

    return () => {
      window.removeEventListener('resize', checkIfMobile);
      window.removeEventListener('orientationchange', checkIfMobile);
    };
  }, []);

  return isMobile;
};
